}
@font-face {
  font-family: JBMI;
  src: url(/curso-html/static/media/JetBrainsMonoNL-Italic.cd03bed2.ttf);
}

html {
  --bg-main: #fff;
  --bg-light: #fafafa;
  --text-main: #2f2f2f;
  --text-light: #3f3f3f;
  --white: #fff;
  --black: #2f2f2f;

  --z-index: 1;
  --z-index-fixed: 2;
  --z-index-tooltip: 3;

  --font-normal: "Poppins";
  --font-code: JBM;

  --font-size-h1: 2rem;
  --font-size-h2: 1.75rem;
  --font-size-h3: 1.5rem;
  --font-size-h4: 1.25rem;
  --font-size-normal: 1.1rem;
  --font-size-small: 0.8rem;
}

html.dark {
  --bg-main: #2f2f2f;
  --bg-light: #373737;
  --text-main: #fff;
  --text-light: #fafafa;
}

html.html {
  --color-main: rgb(239, 101, 42);
  --color-main70: rgba(239, 101, 42, 0.7);
  --color-main50: rgba(239, 101, 42, 0.5);
  --color-main30: rgba(239, 101, 42, 0.3);
  --color-main10: rgba(239, 101, 42, 0.1);
}

html.css {
  --color-main: rgb(48, 169, 220);
  --color-main70: rgba(48, 169, 220, 0.7);
  --color-main50: rgba(48, 169, 220, 0.5);
  --color-main30: rgba(48, 169, 220, 0.3);
  --color-main10: rgba(48, 169, 220, 0.1);
}

* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  min-height: 100vh;
  overflow: hidden;
  padding: 0;
  margin: 0;
  color: #2f2f2f;
  color: var(--text-main);
  background-color: #fff;
  background-color: var(--bg-main);
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  display: inline-flex;
  align-items: center;
  grid-gap: 0.25rem;
  gap: 0.25rem;
  color: var(--color-main);
  text-decoration: none;
}

a > img {
  width: 1rem;
}

a:hover {
  color: var(--color-main70);
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
}

h1 {
  font-size: 2rem;
  font-size: var(--font-size-h1);
  margin: 1rem 0;
}

h2 {
  font-size: 1.75rem;
  font-size: var(--font-size-h2);
  margin: 0.5rem 0;
}

h3,
summary {
  font-size: 1.5rem;
  font-size: var(--font-size-h3);
  margin: 0.25rem 0;
}

h4 {
  font-size: 1.25rem;
  font-size: var(--font-size-h4);
  margin: 0.25rem 0;
}

p,
a,
blockquote,
cite,
pre,
code,
td,
th,
caption,
input,
button,
legend,
li,
figcaption,
label {
  font-size: 1.1rem;
  font-size: var(--font-size-normal);
}

p {
  line-height: 150%;
}

blockquote {
  margin: 2rem 0;
  padding: 1rem;
  border-radius: 0.25rem;
  border: 2px solid var(--color-main);
}

pre,
code {
  font-family: JBM, source-code-pro, Menlo, Monaco, Consolas,
    "Courier New", monospace;
  font-family: var(--font-code), source-code-pro, Menlo, Monaco, Consolas,
    "Courier New", monospace;
  border-radius: 0;
}

code {
  color: var(--color-main);
  background-color: var(--color-main10);
  padding: 0 0.5rem;
}

button,
input {
  font-family: "Poppins";
  font-family: var(--font-normal);
  border: none;
  background-color: transparent;
  outline: none;
}

input::-webkit-input-placeholder {
  font-family: "Poppins";
  font-family: var(--font-normal);
}

input:-ms-input-placeholder {
  font-family: "Poppins";
  font-family: var(--font-normal);
}

input::placeholder {
  font-family: "Poppins";
  font-family: var(--font-normal);
}

ul,
ol {
  padding: 1rem;
}

ul {
  list-style: none;
}

li {
  color: var(--color-main);
}

strong,
em,
u {
  color: var(--color-main);
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  margin: 1rem 0;
}

.hljs {
  line-height: 150%;
}

.hljs-attr,
.hljs-comment,
.hljs-params {
  font-family: "JBMI";
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.button-circle {
  position: fixed;
  bottom: 2rem;
  z-index: 3;
  z-index: var(--z-index-tooltip);
  height: 3rem;
  width: 3rem;
  display: grid;
  place-items: center;
  font-size: 1.5rem;
  border-radius: 50%;
  background-color: #fff;
  background-color: var(--bg-main);
  color: #3f3f3f;
  color: var(--text-light);
  border: 2px solid #3f3f3f;
  border: 2px solid var(--text-light);
  cursor: pointer;
  transition-property: background-color, color;
  transition-duration: 0.3s;
}

.code,
.emphasis {
  display: inline-block;
  padding: 0 0.5rem;
  background-color: var(--color-main10);
  color: var(--color-main);
  font-weight: inherit;
}

.code {
  font-family: JBM;
  font-family: var(--font-code);
}

.hljs {
  border-radius: 0.5rem;
  border: 3px solid transparent;
}

.pagination {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.pagination-button {
  padding: 1rem;
  color: #fff;
  background-color: var(--color-main);
  border-radius: 0.5rem;
  transition: 0.3s;
}

.pagination-button:hover {
  background-color: var(--color-main70);
  color: #fff;
  text-decoration: none;
}

.results-box::-webkit-scrollbar {
  -webkit-appearance: none;
          appearance: none;
  width: 0;
}

.wrapper::-webkit-scrollbar,
.hljs::-webkit-scrollbar {
  width: 0.75rem;
  height: 0.75rem;
}

.wrapper::-webkit-scrollbar-thumb,
.hljs::-webkit-scrollbar-thumb {
  background-color: #3f3f3f;
  background-color: var(--text-light);
  border-radius: 0.5rem;
}

.wrapper::-webkit-scrollbar-thumb {
  border: 0.125rem solid #fff;
  border: 0.125rem solid var(--bg-main);
}

.theme-toggle {
  right: 6rem;
}

.menu-toggle {
  right: 2rem;
}

.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
}

.iframe {
  margin: 2rem 0;
}

.iframe iframe {
  width: 100%;
  min-height: 50vh;
  background-color: #fff;
  border: 1px solid #ccc;
}

.iframe-title {
  padding: 1rem;
  display: inline-block;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  background-color: var(--color-container);
  border: 1px solid #ccc;
  border-bottom: none;
  border-radius: 0.25rem 0.25rem 0 0;
}

@media screen and (min-width: 992px) {
  html {
    --font-size-h1: 2.5rem;
    --font-size-h2: 2.25rem;
    --font-size-h3: 2rem;
    --font-size-h4: 1.75rem;
    --font-size-normal: 1.2rem;
    --font-size-small: 1rem;
  }
  .theme-toggle {
    top: 1rem;
    bottom: auto;
    bottom: initial;
    right: 1rem;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-index-tooltip);
  width: 100%;
  height: 5rem;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: 1rem;
  gap: 1rem;
  background-color: var(--black);
  color: var(--white);
  border-bottom: 1px solid var(--white);
}

.logo {
  display: none;
  grid-gap: 1rem;
  gap: 1rem;
}

.logo > svg {
  width: 2.5rem;
}

.seeker {
  position: relative;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 3rem;
  color: var(--white);
}

.seeker > input {
  padding-left: 1rem;
  color: var(--white);
}

.seeker > input:focus ~ .seeker-line {
  width: 100%;
}

.seeker > input::-webkit-input-placeholder {
  color: var(--white);
}

.seeker > input:-ms-input-placeholder {
  color: var(--white);
}

.seeker > input::placeholder {
  color: var(--white);
}

.seeker-line {
  position: absolute;
  top: 100%;
  left: 0;
  width: 0;
  height: 1px;
  background-color: var(--white);
  transition: 0.3s;
}

.seeker-button {
  width: 100%;
  height: 100%;
  display: grid;
  place-content: center;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--white);
}

.seeker-button:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.results-box {
  position: absolute;
  top: 110%;
  right: 0;
  width: 300px;
  min-height: 150px;
  max-height: calc(100vh - 5rem);
  background-color: var(--black);
  overflow-y: auto;
  background-color: var(--white);
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
  transition: 0.3s;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: top right;
          transform-origin: top right;
}

.results-box.open {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.results-close {
  position: absolute;
  top: 0;
  right: 0;
  width: 3rem;
  height: 3rem;
  display: grid;
  place-content: center;
  background-color: var(--black);
  font-size: 1.5rem;
  cursor: pointer;
}

.results-topic_title {
  margin: 0;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  font-size: var(--font-size-small);
  letter-spacing: 2px;
  background-color: var(--black);
}

.result-link {
  display: block;
  width: 100%;
  font-size: var(--font-normal);
  padding: 1rem 2rem;
  color: var(--black);
  text-decoration: none;
}

.results-404 {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 1rem;
  display: grid;
  place-content: center;
  color: var(--black);
  font-size: var(--font-size-h3);
  text-align: center;
  line-height: 150%;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 2px;
}

.result-link:hover {
  text-decoration: none;
  background-color: rgba(0, 0, 0, 0.1);
  color: var(--black);
}

.results-topic > .result-temmary:nth-child(even) {
  background-color: rgba(0, 0, 0, 0.05);
}

@media screen and (min-width: 992px) {
  .header {
    padding-right: 5rem;
  }
  .logo {
    display: flex;
  }
  .seeker {
    width: 300px;
  }
  .results-box {
    max-width: 500px;
  }
}

.main {
  position: fixed;
  top: 5rem;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: var(--bg-main);
  transition-property: background-color, color;
  transition-duration: 0.3s;
}

.main-wrapper {
  padding: 1rem;
}

.main p {
  margin: 2rem 0;
}

@media screen and (min-width: 992px) {
  .main {
    width: calc(100% - 400px);
  }
}

.nav {
  position: fixed;
  top: 5rem;
  right: -420px;
  bottom: 0;
  z-index: var(--z-index-fixed);
  width: 100%;
  max-width: 400px;
  background-color: var(--bg-light);
  transition: 0.3s;
}

.nav.open {
  right: 0;
}

.nav-wrapper {
  padding: 2rem;
}

.topic {
  margin: 2rem 0;
}

.topic[open] svg {
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
}

.topic-title {
  display: flex;
  align-items: center;
  font-size: var(--font-size-normal);
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: uppercase;
  cursor: pointer;
  color: var(--text-light);
}

.topic svg {
  font-size: 1.5rem;
}

.temmary-link {
  display: block;
  padding: 0.5rem;
  margin: 0.25rem 0;
  color: var(--text-light);
  transition: 0.3s;
}

.temmary-link.active {
  padding-left: 1rem;
  font-weight: 600;
  border-left: 5px solid var(--color-main);
}

.temmary-link:hover {
  color: var(--text-main);
  text-decoration: none;
  background-color: var(--color-main10);
}

@media screen and (min-width: 992px) {
  .nav {
    right: 0;
  }
  .menu-toggle {
    display: none;
  }
  .temmary-link {
    font-size: var(--font-size-small);
  }
}

