@import-normalize;

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap");

@font-face {
  font-family: JBM;
  src: url("./JetBrainsMonoNL-Regular.ttf");
}
@font-face {
  font-family: JBMI;
  src: url("./JetBrainsMonoNL-Italic.ttf");
}

html {
  --bg-main: #fff;
  --bg-light: #fafafa;
  --text-main: #2f2f2f;
  --text-light: #3f3f3f;
  --white: #fff;
  --black: #2f2f2f;

  --z-index: 1;
  --z-index-fixed: 2;
  --z-index-tooltip: 3;

  --font-normal: "Poppins";
  --font-code: JBM;

  --font-size-h1: 2rem;
  --font-size-h2: 1.75rem;
  --font-size-h3: 1.5rem;
  --font-size-h4: 1.25rem;
  --font-size-normal: 1.1rem;
  --font-size-small: 0.8rem;
}

html.dark {
  --bg-main: #2f2f2f;
  --bg-light: #373737;
  --text-main: #fff;
  --text-light: #fafafa;
}

html.html {
  --color-main: rgb(239, 101, 42);
  --color-main70: rgba(239, 101, 42, 0.7);
  --color-main50: rgba(239, 101, 42, 0.5);
  --color-main30: rgba(239, 101, 42, 0.3);
  --color-main10: rgba(239, 101, 42, 0.1);
}

html.css {
  --color-main: rgb(48, 169, 220);
  --color-main70: rgba(48, 169, 220, 0.7);
  --color-main50: rgba(48, 169, 220, 0.5);
  --color-main30: rgba(48, 169, 220, 0.3);
  --color-main10: rgba(48, 169, 220, 0.1);
}

* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  min-height: 100vh;
  overflow: hidden;
  padding: 0;
  margin: 0;
  color: var(--text-main);
  background-color: var(--bg-main);
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  display: inline-flex;
  align-items: center;
  gap: 0.25rem;
  color: var(--color-main);
  text-decoration: none;
}

a > img {
  width: 1rem;
}

a:hover {
  color: var(--color-main70);
  text-decoration-line: underline;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
}

h1 {
  font-size: var(--font-size-h1);
  margin: 1rem 0;
}

h2 {
  font-size: var(--font-size-h2);
  margin: 0.5rem 0;
}

h3,
summary {
  font-size: var(--font-size-h3);
  margin: 0.25rem 0;
}

h4 {
  font-size: var(--font-size-h4);
  margin: 0.25rem 0;
}

p,
a,
blockquote,
cite,
pre,
code,
td,
th,
caption,
input,
button,
legend,
li,
figcaption,
label {
  font-size: var(--font-size-normal);
}

p {
  line-height: 150%;
}

blockquote {
  margin: 2rem 0;
  padding: 1rem;
  border-radius: 0.25rem;
  border: 2px solid var(--color-main);
}

pre,
code {
  font-family: var(--font-code), source-code-pro, Menlo, Monaco, Consolas,
    "Courier New", monospace;
  border-radius: 0;
}

code {
  color: var(--color-main);
  background-color: var(--color-main10);
  padding: 0 0.5rem;
}

button,
input {
  font-family: var(--font-normal);
  border: none;
  background-color: transparent;
  outline: none;
}

input::placeholder {
  font-family: var(--font-normal);
}

ul,
ol {
  padding: 1rem;
}

ul {
  list-style: none;
}

li {
  color: var(--color-main);
}

strong,
em,
u {
  color: var(--color-main);
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  margin: 1rem 0;
}

.hljs {
  line-height: 150%;
}

.hljs-attr,
.hljs-comment,
.hljs-params {
  font-family: "JBMI";
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.button-circle {
  position: fixed;
  bottom: 2rem;
  z-index: var(--z-index-tooltip);
  height: 3rem;
  width: 3rem;
  display: grid;
  place-items: center;
  font-size: 1.5rem;
  border-radius: 50%;
  background-color: var(--bg-main);
  color: var(--text-light);
  border: 2px solid var(--text-light);
  cursor: pointer;
  transition-property: background-color, color;
  transition-duration: 0.3s;
}

.code,
.emphasis {
  display: inline-block;
  padding: 0 0.5rem;
  background-color: var(--color-main10);
  color: var(--color-main);
  font-weight: inherit;
}

.code {
  font-family: var(--font-code);
}

.hljs {
  border-radius: 0.5rem;
  border: 3px solid transparent;
}

.pagination {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.pagination-button {
  padding: 1rem;
  color: #fff;
  background-color: var(--color-main);
  border-radius: 0.5rem;
  transition: 0.3s;
}

.pagination-button:hover {
  background-color: var(--color-main70);
  color: #fff;
  text-decoration: none;
}

.results-box::-webkit-scrollbar {
  appearance: none;
  width: 0;
}

.wrapper::-webkit-scrollbar,
.hljs::-webkit-scrollbar {
  width: 0.75rem;
  height: 0.75rem;
}

.wrapper::-webkit-scrollbar-thumb,
.hljs::-webkit-scrollbar-thumb {
  background-color: var(--text-light);
  border-radius: 0.5rem;
}

.wrapper::-webkit-scrollbar-thumb {
  border: 0.125rem solid var(--bg-main);
}

.theme-toggle {
  right: 6rem;
}

.menu-toggle {
  right: 2rem;
}

.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
}

.iframe {
  margin: 2rem 0;
}

.iframe iframe {
  width: 100%;
  min-height: 50vh;
  background-color: #fff;
  border: 1px solid #ccc;
}

.iframe-title {
  padding: 1rem;
  display: inline-block;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  background-color: var(--color-container);
  border: 1px solid #ccc;
  border-bottom: none;
  border-radius: 0.25rem 0.25rem 0 0;
}

@media screen and (min-width: 992px) {
  html {
    --font-size-h1: 2.5rem;
    --font-size-h2: 2.25rem;
    --font-size-h3: 2rem;
    --font-size-h4: 1.75rem;
    --font-size-normal: 1.2rem;
    --font-size-small: 1rem;
  }
  .theme-toggle {
    top: 1rem;
    bottom: initial;
    right: 1rem;
  }
}
