.nav {
  position: fixed;
  top: 5rem;
  right: -420px;
  bottom: 0;
  z-index: var(--z-index-fixed);
  width: 100%;
  max-width: 400px;
  background-color: var(--bg-light);
  transition: 0.3s;
}

.nav.open {
  right: 0;
}

.nav-wrapper {
  padding: 2rem;
}

.topic {
  margin: 2rem 0;
}

.topic[open] svg {
  transform: rotateX(180deg);
}

.topic-title {
  display: flex;
  align-items: center;
  font-size: var(--font-size-normal);
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: uppercase;
  cursor: pointer;
  color: var(--text-light);
}

.topic svg {
  font-size: 1.5rem;
}

.temmary-link {
  display: block;
  padding: 0.5rem;
  margin: 0.25rem 0;
  color: var(--text-light);
  transition: 0.3s;
}

.temmary-link.active {
  padding-left: 1rem;
  font-weight: 600;
  border-left: 5px solid var(--color-main);
}

.temmary-link:hover {
  color: var(--text-main);
  text-decoration: none;
  background-color: var(--color-main10);
}

@media screen and (min-width: 992px) {
  .nav {
    right: 0;
  }
  .menu-toggle {
    display: none;
  }
  .temmary-link {
    font-size: var(--font-size-small);
  }
}
