.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-index-tooltip);
  width: 100%;
  height: 5rem;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  background-color: var(--black);
  color: var(--white);
  border-bottom: 1px solid var(--white);
}

.logo {
  display: none;
  gap: 1rem;
}

.logo > svg {
  width: 2.5rem;
}

.seeker {
  position: relative;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 3rem;
  color: var(--white);
}

.seeker > input {
  padding-left: 1rem;
  color: var(--white);
}

.seeker > input:focus ~ .seeker-line {
  width: 100%;
}

.seeker > input::placeholder {
  color: var(--white);
}

.seeker-line {
  position: absolute;
  top: 100%;
  left: 0;
  width: 0;
  height: 1px;
  background-color: var(--white);
  transition: 0.3s;
}

.seeker-button {
  width: 100%;
  height: 100%;
  display: grid;
  place-content: center;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--white);
}

.seeker-button:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.results-box {
  position: absolute;
  top: 110%;
  right: 0;
  width: 300px;
  min-height: 150px;
  max-height: calc(100vh - 5rem);
  background-color: var(--black);
  overflow-y: auto;
  background-color: var(--white);
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
  transition: 0.3s;
  transform: scale(0);
  transform-origin: top right;
}

.results-box.open {
  transform: scale(1);
}

.results-close {
  position: absolute;
  top: 0;
  right: 0;
  width: 3rem;
  height: 3rem;
  display: grid;
  place-content: center;
  background-color: var(--black);
  font-size: 1.5rem;
  cursor: pointer;
}

.results-topic_title {
  margin: 0;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  font-size: var(--font-size-small);
  letter-spacing: 2px;
  background-color: var(--black);
}

.result-link {
  display: block;
  width: 100%;
  font-size: var(--font-normal);
  padding: 1rem 2rem;
  color: var(--black);
  text-decoration: none;
}

.results-404 {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 1rem;
  display: grid;
  place-content: center;
  color: var(--black);
  font-size: var(--font-size-h3);
  text-align: center;
  line-height: 150%;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 2px;
}

.result-link:hover {
  text-decoration: none;
  background-color: rgba(0, 0, 0, 0.1);
  color: var(--black);
}

.results-topic > .result-temmary:nth-child(even) {
  background-color: rgba(0, 0, 0, 0.05);
}

@media screen and (min-width: 992px) {
  .header {
    padding-right: 5rem;
  }
  .logo {
    display: flex;
  }
  .seeker {
    width: 300px;
  }
  .results-box {
    max-width: 500px;
  }
}
