.main {
  position: fixed;
  top: 5rem;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: var(--bg-main);
  transition-property: background-color, color;
  transition-duration: 0.3s;
}

.main-wrapper {
  padding: 1rem;
}

.main p {
  margin: 2rem 0;
}

@media screen and (min-width: 992px) {
  .main {
    width: calc(100% - 400px);
  }
}
